.link{
  color: white;
  position: relative;
  text-decoration: none;
  margin-bottom: 30px;
  font-size: 25px;
  font-weight: bold;
  font-family: 'Roboto', sans-serif;
}

.link:after{
  text-decoration: none;
  content: '';
  position: absolute;
  width: 0; height: 3px;
  display: block;
  margin-top: 5px;
  right: 0;
  background: #fff;
  transition: width .2s ease;
  -webkit-transition: width .2s ease;
}

.link:hover:after{
  text-decoration: none;
  width: 100%;
  left: 0;
  background: #fff;
}

.projects-container {
  background-color: #6B9080;
  padding-bottom:10%;
