.dot-navbar {
  position:absolute;
  right:0;
}

.dot {
  display:inline-block;
  width:20px;
  height:20px;
  background-color:#dddddd;
  border-radius:10px;
  z-index:999;
  content:'';
  color:transparent;
  font-size:14px;
  line-height:17px;
  vertical-align:middle;
  margin:5px 100px 10px 0;
  font-weight:300;
}
.dot:hover {
  color:white;
  background-color:#6d6d6d;
}
.dot span {
  margin:0px 0px 20px 15px;
  position:relative;
  top:0px;
  left:-100px;
}

